.Center{
    width: 100%;
    display:flex;
    flex-direction:column;
    gap:1rem;
    overflow-y:auto;
}
.skillList{
   
    display:flex;
    justify-content:space-around;
}
.skillList>li{
    list-style:none;
    width:6rem;
    height:3rem;
    margin-right:2px;
    display:flex;
    justify-content:center;
    align-items:center;
    
}
.skillList>li>img{
   width:30px;
}
::-webkit-scrollbar {
  width: 5px;
  height: 3px;
  background-color: #aaa; /* or add it to the track */
}
::-webkit-scrollbar-thumb {
  background: rgb(100, 99, 99);
}

@media only screen and (max-width: 600px){
  	.Center{
      display:flex;
      flex-direction:column;
      height:100%;
      
    }
    .mySkills{
        margin-top:1rem;
    }
    .skillList>li{
        padding-right:2px;
    }
    .skillList>li>img{
     width:20px;
}
.skillList>li>span{
    font-size:16px;
}
    
}
