.App{
  padding-top: 1rem;
  width:100%;
  height:100vh;
  position: relative;
  display: grid;
  grid-template-columns: 20rem auto 20rem;
  gap: 1rem;
  padding:1rem ;
  overflow-y:scroll;
}

@media only screen and (max-width: 600px){
   h1{
     font-size:27px;
  }
  	.App{
      display:flex;
      flex-direction:column;
      height:100%;
    }
    
}
