.leftSide{
    display: flex;
    flex-direction: column;
    gap:0.75rem;
    
}

.profileCard{
    display:flex;
    flex-direction: column;
    
}
.images{
    display:flex;
    flex-direction: column;
    position:relative;
    object-fit:cover;

}
.coverPic{
    height:100px;
    width:100%;
    object-fit: cover;

    
}
.profilePic{
    height: 5rem;
    width:5rem;
    border-radius: 50%;
    border: 3px solid white;
    position: absolute;
    align-self: center;
    object-fit:center;
    top: 3.5rem;
}
.names{
    display: flex;
    flex-direction: column;
     align-items: center;
    margin-top: 2.0rem;
}


.contactsLeftSide{
    margin:0.5rem 0rem 1rem 0rem;
}