.Banner{
    display:flex;
    flex-direction:column;
    gap:0.75rem;
    margin-top: 0.4rem;
    border-radius:10px;
    height:5rem;
  
}
.conDetails{
    display:flex;
    flex-direction:column;
}
.conDetails>span{
    margin-bottom: 5px;
}
.Banner>h3{
    display:flex;
    align-self:center;

}
.socLinks{
   display:flex;
   list-style:none;
   gap:10px;

}
.socLinks>li{
    width: 30px;
    height: 30px;
    text-decoration:none;
    border:1px solid rgb(204, 203, 203);
    display: flex;
    justify-content: center;
    align-items:center;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.socLinks>li>img{
    height: 20px;
    padding: 2px;
    width: 20px;
    

}
    
