.contactForm{
    display:flex;
    flex-direction: column;
    align-self:center;
    width:100%;
    
}
.contactForm>input{
    width:95%;
    margin-bottom:10px;
    height:2rem;
    padding:5px;
    outline:none;
    border:1px solid grey;
    border-radius: 5px ;
    -webkit-border-radius: 5px ;
    -moz-border-radius: 5px ;
    -ms-border-radius: 5px ;
    -o-border-radius: 5px ;
}
.textArea{
    padding:5px;
    max-width:95%;
    min-width:95%;
    min-height:5rem;
    max-height:10rem;
    height:4rem;
    outline:none;
    border:1px solid grey;
    border-radius: 5px;
    
}
.submitBtn{
    width:4rem;
    margin: 10px 0px;
    padding:5px 0px;
    display:flex;
    align-items:center;
    justify-content:center;
    border-radius: 5px;
    border:none;
    color:white;
    background-color:black;
    
}